
@keyframes scale {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-scale {
  animation: scale 0.3s ease forwards;
}

@keyframes shake {
  10%,
  90% {
    transform: translateX(-1px);
  }

  20%,
  80% {
    transform: translateX(2px);
  }

  30%,
  50%,
  70% {
    transform: translateX(-4px);
  }

  40%,
  60% {
    transform: translateX(4px);
  }
}

.animate-shake {
  animation: shake 0.3s;
}

@keyframes pulseFlow {
  0%, 100% {
    transform: scale(0.9);
    opacity: 0.4;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-pulse-flow {
  animation: pulseFlow 2s ease-in-out infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-25%);
  }
  50% {
    transform: none;
  }
}

.animate-bounce {
  animation: bounce 1s cubic-bezier(0.8, 0, 1, 1) infinite;
}

@keyframes successCircle {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes successCheck {
  0% {
    stroke-dashoffset: 100;
    opacity: 0;
  }
  60% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

.success-circle {
  animation: successCircle 0.6s cubic-bezier(0.22, 0.61, 0.36, 1) forwards;
}

.success-check {
  stroke-dasharray: 100;
  stroke-dashoffset: 100;
  animation: successCheck 0.9s cubic-bezier(0.22, 0.61, 0.36, 1) forwards;
  animation-delay: 0.3s;
}

.connect-btn {
  transition: all 0.2s ease;
}

.connect-btn:hover {
  transform: scale(1.05);
}

.connect-btn:active {
  transform: scale(0.95);
}

.delay-100 {
  animation-delay: 0s;
}

.delay-200 {
  animation-delay: 0.4s;
}

.delay-300 {
  animation-delay: 0.8s;
}

.step-view {
  display: none;
}

.verification-input {
  width: 2.5rem;
  height: 2.5rem;
  text-align: center;
  border-radius: 0.5rem;
  font-size: 1.125rem;
  font-weight: 600;
  border: 1px solid rgb(209 213 219);
}

.verification-input:focus {
  outline: none;
  border-color: #5B47FB;
  box-shadow: 0 0 0 2px rgba(91, 71, 251, 0.2);
}

.verification-input.filled {
  background-color: rgb(249 250 251);
  border-color: #5B47FB;
}

.verification-input.error {
  border-color: rgb(239 68 68);
  animation: shake 0.3s;
}

.verification-button {
  display: block;
  width: 100%;
  text-align: center;
  color: #5B47FB;
  font-weight: 500;
  padding: 0.5rem;
  border-radius: 0.375rem;
  transition: color 0.2s;
}

.verification-button:hover {
  color: #4936E8;
  background-color: rgba(91, 71, 251, 0.05);
}

@keyframes overlayFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes overlayContentSlideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes logoFloat {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes pulseGlow {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(91, 71, 251, 0.4);
  }
  50% {
    box-shadow: 0 0 20px 3px rgba(91, 71, 251, 0.2);
  }
}

.redirect-overlay-enter {
  animation: overlayFadeIn 0.3s ease-out;
}

.redirect-overlay-content {
  animation: overlayContentSlideUp 0.4s ease-out;
}

.redirect-logo-container {
  animation: logoFloat 2s ease-in-out infinite, pulseGlow 2s ease-in-out infinite;
}

.redirect-dots {
  display: flex;
  gap: 4px;
  justify-content: center;
  margin-bottom: 8px;
}

.redirect-dot {
  width: 6px;
  height: 6px;
  background-color: #5B47FB;
  border-radius: 50%;
  opacity: 0.3;
  animation: dotPulse 1s ease-in-out infinite;
}

.redirect-dot:nth-child(2) {
  animation-delay: 0.2s;
}

.redirect-dot:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes dotPulse {
  0%, 100% {
    opacity: 0.3;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

.az-logo {
  font-weight: 700;
  font-size: 28px;
  font-family: Poppins, sans-serif;
  text-transform: lowercase;
  color: #5b47fb;
  letter-spacing: -1px;
  display: flex;
  align-items: center;
  position: relative;
  top: -2px;
}
